import { makeAutoObservable } from 'mobx';

import { ChessColor } from '../../components/Board/TemplateBoard/types';

export interface GameData {
  isPlaying: boolean; //   
  isVersusAi: boolean; //   
  isAutoPromotion: boolean; //  
  isColoredMoves: boolean; //   
  isConfirmSteps: boolean; //      
  isAudioOn: boolean; //  
  difficult: number | null; // 
  myColor: ChessColor; //  
}

const initGameData: GameData = {
  isVersusAi: false,
  isPlaying: false,
  isAutoPromotion: false,
  isColoredMoves: true,
  isConfirmSteps: false,
  isAudioOn: false,
  difficult: null,
  myColor: 'w',
};

export class GameStore {
  public game: GameData = initGameData;

  constructor() {
    makeAutoObservable(this);
  }

  public setGameData = (data: GameData) => {
    this.game = { ...this.game, ...data };
  };
}

export default new GameStore();
